import { Injectable, Inject } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';
import { ServiceUrl } from './ServiceUrl';
@Injectable()
export class GetLaugisticService {
    urlService = new ServiceUrl();
    constructor(private http: Http) {

    }
  
    GetLaugisticName()
    {
        let body = JSON.stringify({});
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'get', headers: headers });

        return this.http.get(this.urlService.baseUrl + "Logistics/getAllLogistics", options)
            .map(res => res.json());


    }
    AddLaugisticData(Lougistic_Id,username,password,fullName)
    {
        let body = JSON.stringify({ LogisticsId: Lougistic_Id,UserName:username, Password: password,FullName:fullName});
        console.log(body);
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "Logistics/addLogisticUser", body, options)
            .map(res => res.json());
    }
    GetAllCitiesService()
    {
        let body = JSON.stringify({});
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'get', headers: headers });

        return this.http.get(this.urlService.baseUrl + "City/getAllCities", options)
            .map(res => res.json());


    }
    getDataByCities(cityId)
    {
        
        let body = JSON.stringify({});
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'get', headers: headers });

        return this.http.get(this.urlService.baseUrl + "Chemist/getChemistsListByCity?CityId="+cityId, options)
            .map(res => res.json());

    }

    
  
}


